.fade-in-animation {
    opacity: 1;
    -webkit-animation: fade-in 0.5s ease-in;
            animation: fade-in 0.5s ease-in;
  }
  
  @-webkit-keyframes fade-in {
    0% { opacity: 0 }
    100% { opacity: 1}
  }
  
  @keyframes fade-in {
    0% { opacity: 0 }
    100% { opacity: 1}
  }

  .transform{
    transition: 0.5s ease-in-out;
  }
  
  .transform:hover{
    -webkit-transform: scale(1.03);
            transform: scale(1.03);
  }

